import React, { useEffect } from "react";
import { Badge, Card, Col, Dropdown, Form, Row } from "react-bootstrap";
import { BiSearch } from "react-icons/bi";
import BootstrapTable from "react-bootstrap-table-next";
import paginationFactory from "react-bootstrap-table2-paginator";
import { useState } from "react";
import { RiFilter2Fill } from "react-icons/ri";
import { useDispatch, useSelector } from "react-redux";
import { getAdminDashboardAptList } from "../../../app/features/adminDashboard/adminDashboard.slice";

export default function AdminAppointments() {
  const [selectedOptions, setSelectedOptions] = useState(null);
  const [searchValue, setSearchValue] = useState();
  const dispatch = useDispatch();
  const { getAllApt } = useSelector((state) => state?.adminDashboard);

  useEffect(() => {
    const finalData = {
      pageNo: 0,
      size: 0,
      isPagination: false,
      search: searchValue ? searchValue : null,
      statusId:
        selectedOptions === "205"
          ? 205
          : selectedOptions === "204"
          ? 204
          : selectedOptions === "206"
          ? 206
          : selectedOptions === "203"
          ? 203
          : selectedOptions === "201"
          ? 201
          : selectedOptions === "202"
          ? 202
          : null,
    };
    dispatch(getAdminDashboardAptList(finalData));
  }, [dispatch, searchValue, selectedOptions]);

  const renderBadge = (status) => {
    return status === 204 ? (
      <Badge pill bg="info" className="upcoming-badge">
        Upcoming
      </Badge>
    ) : status === 201 ? (
      <Badge pill bg="primary" className="pending-badge">
        Pending
      </Badge>
    ) : status === 205 ? (
      <Badge pill bg="danger" className="missed-badge">
        Missed
      </Badge>
    ) : status === 206 ? (
      <Badge pill bg="light" className="completed-badge">
        Completed
      </Badge>
    ) : status === 203 ? (
      <Badge pill bg="warning" className="cancelled-badge">
        Cancelled
      </Badge>
    ) : (
      <Badge>Booked</Badge>
    );
  };

  const columns = [
    {
      dataField: "id",
      text: "ID",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "doctorName",
      text: "Doctor Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "patientName",
      text: "Patient Name",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "appointmentDate",
      text: "Date",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
      formatter: (cell) => {
        const date = new Date(cell);
        const day = date.getDate().toString().padStart(2, "0");
        const month = (date.getMonth() + 1).toString().padStart(2, "0");
        const year = date.getFullYear();
        return `${month}/${day}/${year}`;
      },
    },
    {
      dataField: "appointmentTime",
      text: "Time",
      sort: true,
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
    {
      dataField: "status",
      text: "Status",
      formatter: (text, row) => renderBadge(row?.statusId),
      headerStyle: { backgroundColor: "#F1F1F1" },
    },
  ];

  const customTotal = (from, to, size) => (
    <span className="react-bootstrap-table-pagination-total">
      {from} to {to} out of {size} entries
    </span>
  );

  const paginateOptions = {
    paginationSize: 8,
    pageStartIndex: 1,
    alwaysShowAllBtns: true,
    withFirstAndLast: false,
    hideSizePerPage: true,
    showTotal: true,
    paginationTotalRenderer: customTotal,
    disablePageTitle: true,
    sizePerPageList: [
      {
        text: "10",
        value: 10,
      },
    ],
  };
  const options = [
    { label: "Missed", value: 205 },
    { label: "Upcoming", value: 204 },
    { label: "Completed", value: 206 },
    { label: "Cancelled", value: 203 },
    { label: "Pending", value: 201 },
    { label: "Booked", value: 202 },
  ];
  // const [selectedOptions, setSelectedOptions] = useState(["upcoming"]);

  // const handleOptionChange = (event) => {
  //   const value = event.target.value;

  //   if (selectedOptions.includes(value)) {
  //     setSelectedOptions(selectedOptions.filter((option) => option !== value));
  //   } else {
  //     setSelectedOptions([...selectedOptions, value]);
  //   }
  // };
  return (
    <>
      <Card className="mb-0 ">
        <Card.Body className="p-0">
          <Row className="px-4 pt-3">
            <Col md={4} className="d-flex align-items-center">
              <Card.Title className="mb-0">Appointments</Card.Title>
            </Col>
            <Col
              md={8}
              className="d-flex flex-wrap align-self-center justify-content-end"
            >
              <span className="d-flex justify-content-between align-self-center">
                <Form.Control
                  onKeyDown={(e) => {
                    if (e.key === "Enter") {
                      setSearchValue(e.target.value);
                    }
                  }}
                  onChange={(e) => {
                    if (e.key === "Enter") {
                      setSearchValue(e.target.value);
                    }
                  }}
                  type="text"
                  placeholder="Search"
                  className="search-field-spacing mb-2"
                  aria-label="Search"
                />
                <BiSearch size={22} className="searchbar-icon" />
              </span>
              <Dropdown className="apt-filter-dropdown mb-2">
                <Dropdown.Toggle
                  variant="secondary"
                  id="checkbox-dropdown"
                  className="custom-outline-filter py-2"
                >
                  <RiFilter2Fill size={28} className="color-99" />
                </Dropdown.Toggle>

                <Dropdown.Menu>
                  <Form>
                    {options.map((option) => (
                      <Form.Check
                        key={option.value}
                        type="checkbox"
                        label={option.label}
                        value={option.value}
                        checked={
                          selectedOptions === null
                            ? false
                            : selectedOptions.includes(option.value)
                        }
                        onChange={(event) =>
                          setSelectedOptions(
                            selectedOptions === event.target.value
                              ? null
                              : event.target.value
                          )
                        }
                      />
                    ))}
                  </Form>
                </Dropdown.Menu>
              </Dropdown>
            </Col>
          </Row>
          <span className="patient-datatable apt-badge">
            <BootstrapTable
              columns={columns}
              data={getAllApt ? getAllApt : []}
              keyField="id"
              id="bar"
              pagination={paginationFactory(paginateOptions)}
              // striped
              bordered={false}
              wrapperClasses="table-responsive"
            />
          </span>
        </Card.Body>
      </Card>
    </>
  );
}
