import React from "react";
import { Card, Row, Col } from "react-bootstrap";
import IMAGES from "../../../assets/images";
import Calendar from "./Calendar";
import AdminAppointments from "./AdminAppointments";
import { useDispatch, useSelector } from "react-redux";
import { useEffect } from "react";
import { getAdminDashCount } from "../../../app/features/adminDashboard/adminDashboard.slice";

export default function Dashboard() {
  const dispatch = useDispatch();
  const { adminDashCount } = useSelector((state) => state?.adminDashboard);

  useEffect(() => {
    dispatch(getAdminDashCount());
  }, [dispatch]);
  return (
    <>
      <Row className="main-row patient-dashbaord">
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Patients</Card.Title>
                <h3 className="mb-0">{adminDashCount?.totalPatient}</h3>
              </span>
              <img src={IMAGES.NEW_PATIENT_ICON} alt="total patients" className="color-dk-blue" />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Doctors</Card.Title>
                <h3 className="mb-0">{adminDashCount?.totalDoctor}</h3>
              </span>
              <img src={IMAGES.TOTAL_DOCTORS_ICON} alt="total doctors" className="color-dk-blue" />
            </Card.Body>
          </Card>
        </Col>
        <Col sm={4} xs={12} className="xs-margin-bottom">
          <Card className="h-100">
            <Card.Body className="d-flex justify-content-between align-items-center p-4 display-column-reverse">
              <span>
                <Card.Title>Total Staff</Card.Title>
                <h3 className="mb-0">{adminDashCount?.totalStaff}</h3>
              </span>
              <img src={IMAGES.TOTAL_STAFF_ICON} alt="total staff" className="color-dk-blue"/>
            </Card.Body>
          </Card>
        </Col>
      </Row>

      <Row className="my-3">
        <Col xl={8} xs={12}>
          <Row>
            <Col xs={12} className="mb-3">
              <AdminAppointments />
            </Col>
          </Row>
        </Col>
        <Col xl={4} xs={12}>
          <Row>
            <Col xs={12}>
              <Calendar />
            </Col>
          </Row>
        </Col>
      </Row>
    </>
  );
}
