import React, { useEffect, useState } from "react";
import { Card } from "react-bootstrap";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

import IMAGES from "../../../assets/images";
import { AiFillClockCircle } from "react-icons/ai";
import moment from "moment";
import { getAllAppointment } from "../../../app/features/appointment/appointment.slice";
import { useDispatch, useSelector } from "react-redux";

export default function Calendar() {
  const [startDate, setStartDate] = useState(new Date());
  const [endDate, setEndDate] = useState(null);
  const dispatch = useDispatch();
  const { allAppointment } = useSelector((state) => state?.appointment);

  useEffect(() => {
    const finalData = {
      isDashbaordAppointment: true,
      startdate: startDate
        ? moment(startDate).format("YYYY-MM-DD")
        : moment(new Date()).format("YYYY-MM-DD"),
      endDate: startDate
        ? moment(startDate).format("YYYY-MM-DD")
        : moment(new Date()).format("YYYY-MM-DD"),
      statusId: 202,
    };
    dispatch(getAllAppointment(finalData));
  }, [dispatch, startDate, endDate]);

  const [currentButtonSetIndex, setCurrentButtonSetIndex] = useState(0);

  const handleButtonClick = () => {
    setCurrentButtonSetIndex((currentButtonSetIndex + 1) % 2);
  };

  // const onChange = (dates) => {
  //   const [start, end] = dates;
  //   setStartDate(start);
  //   setEndDate(end);
  // };
  return (
    <>
      <Card className="py-4 m-0">
        <span className="d-flex align-items-center px-4 pb-0 ">
          <img
            src={IMAGES.APPOINTMENT_ICON}
            alt="new patient icon"
            style={{ width: "20px" }}
            className="me-2 color-dk-blue"
          />
          <Card.Title className="m-0">Calendar</Card.Title>
        </span>
        <Card.Body className="px-0">
          <span className="patient-calendar">
            {/* <DatePicker
              selected={startDate}
              onChange={onChange}
              startDate={startDate}
              endDate={endDate}
              selectsRange
              inline
            /> */}
            <DatePicker
              selected={startDate}
              onChange={(date) => setStartDate(date)}
              startDate={startDate}
              inline
            />
          </span>
        </Card.Body>
        <div
          className={
            allAppointment?.length > 0
              ? "patient-custom-scrollbar-admin"
              : "custom-apt-height-admin"
          }
        >
          {allAppointment?.length > 0 ? (
            allAppointment?.map((item, index) => (
              <Card.Body className="mx-4 mb-3 calendar-card">
                <Card.Title style={{ fontSize: "24px" }}>
                  {/* {item.appointmentTitle} */}
                </Card.Title>
                <div className="d-flex align-items-center">
                  <span className="me-3 apt-inner-card p-2 d-flex align-items-center flex-column">
                    <p
                      style={{ fontSize: "26px", fontWeight: 600 }}
                      className="mb-0"
                    >
                      {moment(item?.appointmentDate).format("MMM DD")}
                    </p>
                    <p className="m-0 table-normal-text">
                      <AiFillClockCircle
                        className="me-2"
                        style={{ color: "#999999" }}
                      />
                      {item?.appointmentStartTime}
                    </p>
                  </span>
                  <span style={{ lineHeight: "1.4" }}>
                    <p className="m-0" style={{ fontSize: "18px" }}>
                      {item?.doctorName}
                    </p>
                    {/* <p className="m-0 table-normal-text color-99">
                      (Dermatologist)
                    </p> */}
                  </span>
                </div>
                <span>
                  <p className="color-99 mt-3 mb-2">Reason:</p>
                  <p
                    className="mb-0"
                    style={{ fontSize: "16px", fontWeight: 500 }}
                  >
                    {item?.reasonForAppoinment}
                  </p>
                </span>
              </Card.Body>
            ))
          ) : (
            <div className="px-3">
              <p style={{ fontSize: "20px", fontWeight: 500 }} className="mb-0">
                Upcoming Appointment
              </p>
              <div className="py-2 d-flex justify-content-center align-items-center flex-column">
                <img
                  src={IMAGES.UPCOMING_APPOINTMENT_ICON}
                  alt="upcoming appointment"
                />
                <p
                  className="mb-0 mt-2 color-99 text-center"
                  style={{ fontSize: "20px", fontWeight: 400, width: "54%" }}
                >
                  No appointment scheduled
                </p>
              </div>
            </div>
          )}
        </div>
      </Card>
    </>
  );
}
